import { useSelector, shallowEqual, useDispatch } from 'react-redux'
import { RootState } from '../../../../../setup'

export const ShowWalletAddress = (props: any) => {
    
    return (
        <>
            <div className="modal fade" id="kt_modal_show_wallet_address" tabIndex={-1} aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered mw-650px">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h2 className="fw-bolder">Meta Mask</h2>
                            <div className="btn btn-icon btn-sm btn-active-icon-primary" data-bs-dismiss='modal'>
                                <span className="svg-icon svg-icon-1">
                                    <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                        <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#000000">
                                            <rect fill="#000000" x="0" y="7" width="16" height="2" rx="1" />
                                            <rect fill="#000000" opacity="0.5" transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000)" x="0" y="7" width="16" height="2" rx="1" />
                                        </g>
                                    </svg>
                                </span>
                            </div>
                        </div>
                        <div className="modal-body scroll-y mx-5 mx-xl-15 my-7">
                            <form id="kt_modal_update_email_form" className="form" action="#">
                                <div className="fv-row mb-7">
                                    <label className="fs-6 fw-bold form-label mb-2">
                                        <span>Wallet Address</span>
                                    </label>
                                    <input className="form-control form-control-solid" placeholder="" name="profile_email" value={'oxMetamask'} disabled={true}/>
                                </div>
                                <div className="text-center pt-15">
                                    <button type="reset" className="btn btn-danger me-3" data-bs-dismiss='modal'>Close</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}