import { useSelector, shallowEqual } from 'react-redux'
import {toAbsoluteUrl} from '../../../../../_metronic/helpers'
import { RootState } from '../../../../../setup'
import { FormattedMessage } from 'react-intl'
import { Navigate, Route } from 'react-router'

export const UserSummary = (props: any) => {

    return (
        <>
            <div className="card mb-5 mb-xl-8">
                <div className="card-body pt-0 pt-lg-1">
                    <div className="card">
                        <div className="card-body d-flex flex-center flex-column pt-12 p-9 px-0">
                            <div className="position-relative m-auto w-100 h-150px">
                                {props.userDetail.cover_image == 'default' ? (
                                    <>
                                        <img 
                                            style={{width: 'inherit', height: 'inherit'}} 
                                            // src="https://newwallpapershd.com/wp-content/uploads/2018/11/Technology-Music-Beats-Guitar-Facebook-Covers-Photos.jpg"
                                            src={`${process.env.REACT_APP_API_USER_COVER_IMAGE_URL}/default.jpeg`}
                                            alt=""
                                        />
                                    </>
                                ) : (
                                    <>
                                        <img 
                                            style={{width: 'inherit', height: 'inherit'}} 
                                            // src="https://newwallpapershd.com/wp-content/uploads/2018/11/Technology-Music-Beats-Guitar-Facebook-Covers-Photos.jpg"
                                            src={`${process.env.REACT_APP_API_USER_COVER_IMAGE_URL}/${props.userDetail.cover_image}`}
                                            alt=""
                                        />
                                    </>
                                )}
                            </div>
                            <div 
                                className="position-relative" 
                                style={{ width: '180px', height: '180px', borderRadius: '50%', top: '-25px', left: '10%'}}
                            >
                                <div className="symbol symbol-100px symbol-circle mb-7">
                                    <img src={`${process.env.REACT_APP_API_USER_IMAGE_URL}/${props.userDetail.user_image}`} alt="image" />
                                </div>
                            </div>
                            {/* <a href="#" className="fs-3 text-gray-800 text-hover-primary fw-bolder mb-3">{props.userDetail.full_name}</a> */}
                            {/* <div className="mb-9">
                                <div className="badge badge-lg badge-light-primary d-inline">
                                    {spacedRoles()}
                                </div>
                            </div> */}
                        </div>
                    </div>
                    <div className="d-flex flex-stack fs-4 py-3">
                        <div 
                            className="fw-bolder" 
                        >
                            <FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.DETAILS" />
                            <span className="ms-2 rotate-180">
                                <span className="svg-icon svg-icon-3">
                                    <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                        <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                            <polygon points="0 0 24 0 24 24 0 24" />
                                            <path d="M6.70710678,15.7071068 C6.31658249,16.0976311 5.68341751,16.0976311 5.29289322,15.7071068 C4.90236893,15.3165825 4.90236893,14.6834175 5.29289322,14.2928932 L11.2928932,8.29289322 C11.6714722,7.91431428 12.2810586,7.90106866 12.6757246,8.26284586 L18.6757246,13.7628459 C19.0828436,14.1360383 19.1103465,14.7686056 18.7371541,15.1757246 C18.3639617,15.5828436 17.7313944,15.6103465 17.3242754,15.2371541 L12.0300757,10.3841378 L6.70710678,15.7071068 Z" fill="#000000" fillRule="nonzero" transform="translate(12.000003, 11.999999) rotate(-180.000000) translate(-12.000003, -11.999999)" />
                                        </g>
                                    </svg>
                                </span>
                            </span>
                        </div>
                        <span>
                            <div 
                                className="btn btn-sm btn-light-primary" 
                                onClick={() => props.setShowUserUpdateDialog(true)}
                            >
                                <FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.EDIT" />
                            </div>
                        </span>
                    </div>
                    <div className="separator"></div>
                    <div id="kt_user_view_details" className="show">
                        <div className="pb-5 fs-6">
                            <div className="fw-bolder mt-5"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.FULLNAME" /></div>
                            <div className="text-gray-600">
                                <a href="#" className="text-gray-600 text-hover-primary">{props.userDetail.full_name}</a>
                            </div>
                        </div>
                        <div className="pb-5 fs-6">
                            <div className="fw-bolder mt-5"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.USERNAME" /></div>
                            <div className="text-gray-600">
                                <a href="#" className="text-gray-600 text-hover-primary">{props.userDetail.username}</a>
                            </div>
                        </div>
                        <div className="pb-5 fs-6">
                            <div className="fw-bolder mt-5"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.EMAIL" /></div>
                            <div className="text-gray-600">
                                <a href="#" className="text-gray-600 text-hover-primary">{props.userDetail.email}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}