/* eslint-disable jsx-a11y/anchor-is-valid */
import { CircularProgress } from '@mui/material'
import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { FormattedMessage } from 'react-intl'
import { useSelector } from 'react-redux'
import { RootState } from 'setup'

const tokenType: any = {
    '0' : 'Gold',
    '1': 'Platinum',
    '2': 'Diamond'
}

type Props = {
    transferHistory: Array<any>
}

const UserTransferHistory: React.FC<Props> = ({ transferHistory }) => {

    // const { userID } : any = useSelector<RootState>(
    //     ({auth}) => ({
    //         userID: auth.user?.userID
    //     })
    // )

    // const [transferHistory, setTransferHistory] = useState<Array<any>>([])
    const [loading, setLoading] = useState<boolean>(true)
    const navigate = useNavigate()

    const shrinkWalletAddress = (walletAddress: string) => {
        const walletStart = walletAddress.slice(0, 6)
        const walletEnd = walletAddress.slice(35)
    
        return walletStart + '......' + walletEnd
    }

    // useEffect(() => {
    //     getTransferHistory(userID)
    //     .then((response) => {
    //         if(response.data.success) {
    //             setTransferHistory(response.data.transfer_history)
    //             setLoading(false)
    //         } else {

    //         }
    //     })
    // }, [])

    return (
        <div className={`card`} style={{ marginTop: '40px' }}>
        {/* begin::Header */}
        <div className='card-header border-0 pt-5'>
            <h3 className='card-title align-items-start flex-column'>
                <span className='card-label fw-bolder fs-3 mb-1'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.TITLE' /></span>
                {/* <span className='text-muted mt-1 fw-bold fs-7'>Over 500 new products</span> */}
            </h3>
        </div>
        {/* end::Header */}
        {/* begin::Body */}
        <div className='card-body py-3'>
            {/* begin::Table container */}
            <div className='table-responsive'>
            {/* begin::Table */}
            <table className='table align-middle gs-0 gy-4'>
                {/* begin::Table head */}
                <thead>
                    <tr className='fw-bolder text-muted bg-light'>
                        <th className='ps-4 min-w-150px rounded-start'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.TOKEN_DETAILS' /></th>
                        <th className='min-w-125px text-center'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.QUANTITY' /></th>
                        <th className='min-w-125px text-center'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.FROM' /></th>
                        <th className='min-w-125px text-center'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.TO' /></th>
                        <th className='min-w-200px text-center'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.STATUS' /></th>
                        <th className='min-w-200px text-end rounded-end pe-5 text-center'><FormattedMessage id='ADMIN.VIEW_USER.TRANSFER_HISTORY.TRANSACTION' /></th>
                    </tr>
                </thead>
                {/* end::Table head */}
                {/* begin::Table body */}
                <tbody>
                    {transferHistory.map((data: any) => (
                        <tr>
                            <td>
                            <div className='d-flex align-items-center'>
                                <div className='symbol symbol-50px me-5'>
                                <img
                                    src={`${process.env.REACT_APP_API_TOKEN_IMAGE_URL}/${data.token_image}`}
                                    className=''
                                    alt=''
                                />
                                </div>
                                <div className='d-flex justify-content-start flex-column'>
                                <span className='text-dark fw-bolder text-hover-primary mb-1 fs-6'>
                                    {data.collection_name}
                                </span>
                                <span className='text-muted fw-bold text-muted d-block fs-7'>
                                    <FormattedMessage id='CREATE_EDIT_COLLECTION.TIER' /> : 
                                    <span>{tokenType[data.tier]}</span>
                                </span>
                                </div>
                            </div>
                            </td>
                            <td>
                            <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                {JSON.parse(data.tokenID).length}
                            </span>
                            {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Paid</span> */}
                            </td>
                            <td>
                            <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                {shrinkWalletAddress(data.from_addr)}
                            </span>
                            {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Rejected</span> */}
                            </td>
                            <td>
                            <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                {shrinkWalletAddress(data.to_addr)}
                            </span>
                            </td>
                            <td>
                            <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                {data.transfer_status == '1' && 
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill text-success" viewBox="0 0 16 16">
                                        <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                                    </svg>
                                }

                                {data.transfer_status == '0' && 
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-circle-fill text-danger" viewBox="0 0 16 16">
                                        <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z"/>
                                    </svg>
                                }
                            </span>
                            </td>
                            {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Insurance</span> */}
                            <td className='text-center text-end pe-5'>
                                {data.blockchain == 'ethereum' && 
                                    <a
                                        href={`https://goerli.etherscan.io/tx/${data.tx_hash}`}
                                        className='btn btn-bg-light btn-active-color-primary me-1'
                                    >
                                        <FormattedMessage id='SETTINGS_PAGE.VIEW_ON_ETHERSCAN' />
                                    </a>
                                }

                                {data.blockchain == 'klaytn' && 
                                    <a
                                        href={`https://baobab.scope.klaytn.com/tx/${data.tx_hash}`}
                                        className='btn btn-bg-light btn-active-color-primary me-1'
                                    >
                                        <FormattedMessage id='SETTINGS_PAGE.VIEW_ON_KLAYTNSCOPE' />
                                    </a>
                                }
                                
                            </td>
                        </tr>
                    ))}
                </tbody>
                {/* end::Table body */}
            </table>
            {/* end::Table */}
            </div>
            {/* end::Table container */}
        </div>
        {/* begin::Body */}
        </div>
    )
}

export {UserTransferHistory}
