import { useState } from 'react'
import { useSelector, shallowEqual, useDispatch } from 'react-redux'
import { useNavigate } from 'react-router'
import { Dialog } from '@mui/material';
import { DialogActions } from '@mui/material';
import { DialogContent } from '@mui/material';
import { DialogTitle } from '@mui/material';
import * as Yup from 'yup'
import {useFormik} from 'formik'
import { RootState } from '../../../../../setup'
import * as admin from '../../../auth/redux/AuthRedux'
import {FormattedMessage} from 'react-intl'
import { updatePasswordForAdmin } from '../../api';

const passwordFormValidationSchema = Yup.object().shape({
    newPassword: Yup.string()
      .min(3, 'Minimum 3 symbols')
      .max(50, 'Maximum 50 symbols')
      .required('Password is required'),
    passwordConfirmation: Yup.string()
      .min(3, 'Minimum 3 symbols')
      .max(50, 'Maximum 50 symbols')
      .required('Password is required')
      .oneOf([Yup.ref('newPassword'), null], 'Passwords does not match'),
    adminPassword: Yup.string().min(6, 'Require mininmum 6 characters').required('Admin Password is required')
})

interface PasswordChangeModel {
    newPassword: string
    passwordConfirmation: string
    adminPassword: string
}

const initialValues: PasswordChangeModel = {
    newPassword: '',
    passwordConfirmation: '',
    adminPassword: ''
}

export const UpdatePassword = (props: any) => {

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const [loading, setLoading] = useState<boolean>(false)

    const { adminID, authToken }: any = useSelector<RootState>(
        ({auth}) => ({
            adminID: auth.user?.userID,
            authToken: auth.accessToken
        }), shallowEqual
    )

    const formik = useFormik({
        initialValues,
        validationSchema: passwordFormValidationSchema,
        onSubmit: (values) => {
            setLoading(true)
            setTimeout(() => {

                updatePasswordForAdmin(adminID, values.adminPassword, props.userID, values.newPassword)
                .then((response) => {
                    if(response.data.success) {
                        localStorage.setItem('resSuccess', response.data.message)
                        window.location.reload()
                        setLoading(false)
                    } else {
                        if(response.data.message) {
                            localStorage.setItem('resFailure', response.data.message)
                            window.location.reload()
                            setLoading(false)
                        } else {
                            localStorage.setItem('resFailure', 'Password update failed. Please try agein later.')
                            window.location.reload()
                            setLoading(false)
                        }
                    }
                    
                })
            }, 1000)
        }
    })
    return (
        <>
            <Dialog 
                open={props.showPasswordUpdateDialog} 
                onClose={() => props.setShowPasswordUpdateDialog(false)} 
                aria-labelledby="form-dialog-title"
            >
                <DialogTitle className='px-5 text-center fw-bolder text-muted' id="form-dialog-title">
                    <div className="modal-header">
                        <h2 className="fw-bolder"><FormattedMessage id="ADMIN.VIEW_USER.USER_UPDATE_CARD.PASSWORD.UPDATE_PASSWORD" /></h2>
                        <div className="btn btn-icon btn-sm btn-active-icon-primary" onClick={() => props.setShowPasswordUpdateDialog(false)}>
                            <span className="svg-icon svg-icon-1">
                                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                    <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#000000">
                                        <rect fill="#000000" x="0" y="7" width="16" height="2" rx="1" />
                                        <rect fill="#000000" opacity="0.5" transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000)" x="0" y="7" width="16" height="2" rx="1" />
                                    </g>
                                </svg>
                            </span>
                        </div>
                    </div>
                </DialogTitle>
                <div className='separator'></div>
                <DialogContent>
                    <form style={{ padding: '10px 50px' }} id="kt_modal_update_password_form" className="form" onSubmit={formik.handleSubmit}>
                        <div className="mb-10 fv-row" data-kt-password-meter="true">
                            <div className="mb-1">
                                <label className="required form-label fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.VIEW_USER.USER_UPDATE_CARD.PASSWORD.NEW_PASSWORD" /></label>
                                <div className="position-relative mb-3">
                                    <input 
                                        className="form-control form-control-lg form-control-solid" 
                                        type="password" 
                                        placeholder="New Password" 
                                        autoComplete="off" 
                                        {...formik.getFieldProps('newPassword')}
                                    />
                                    {formik.touched.newPassword && formik.errors.newPassword && (
                                        <div className='fv-plugins-message-container'>
                                            <div className='fv-help-block text-danger'>{formik.errors.newPassword}</div>
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className="text-muted">Use 8 or more characters with a mix of letters, numbers &amp; symbols.</div>
                        </div>
                        <div className="fv-row mb-10">
                            <label className="required form-label fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.VIEW_USER.USER_UPDATE_CARD.PASSWORD.CONFIRM_PASSWORD" /></label>
                            <input 
                                className="form-control form-control-lg form-control-solid" 
                                type="password" 
                                placeholder="Confirm Password" 
                                autoComplete="off"
                                {...formik.getFieldProps('passwordConfirmation')}
                            />
                            {formik.touched.passwordConfirmation && formik.errors.passwordConfirmation && (
                                <div className='fv-plugins-message-container'>
                                    <div className='fv-help-block text-danger'>{formik.errors.passwordConfirmation}</div>
                                </div>
                            )}
                        </div>

                        <div className="fv-row mb-7">
                            <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.USER.ADD_USER.ADMIN_PASSWORD" /></label>
                            <input 
                                type="password" 
                                className="form-control form-control-solid mb-3 mb-lg-0" 
                                placeholder="Admin Password" 
                                {...formik.getFieldProps('adminPassword')}
                            />
                            {formik.touched.adminPassword && formik.errors.adminPassword && (
                                <div className='fv-plugins-message-container'>
                                    <div className='fv-help-block text-danger'>{formik.errors.adminPassword}</div>
                                </div>
                            )}
                        </div>

                        <div className="text-center pt-15">
                            <button 
                                type="reset" 
                                className="btn btn-light me-3" 
                                onClick={() => props.setShowPasswordUpdateDialog(false)}
                            >
                                Discard
                            </button>
                            <button type="submit" className="btn btn-primary">
                                <span className="indicator-label">Submit</span>
                                {loading &&
                                    <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                                }
                            </button>
                        </div>
                    </form>
                </DialogContent>
            </Dialog>
        </>
    )
}