import { useEffect, useState } from 'react'
import {ConnectedAccounts} from './components/ConnectedAccounts'
import {ShowWalletAddress} from './components/ShowWalletAddressBox'
import {UpdatePassword} from './components/UpdatePasswordBox'
import {UserSummary} from './components/UserSummary'
import {UserUpdateCard} from './components/UserUpdateCard'
import {UserUpdateWithAddress} from './components/UserUpdateWithAddress'
import {ViewUserHeader} from './components/ViewUserHeader'
import {AlertDanger, AlertSuccess} from '../../alerts/Alerts'
import { shallowEqual, useSelector } from 'react-redux'
import { RootState } from '../../../../setup'
import { useLocation } from 'react-router-dom'
import { getUserDetail } from '../api'
import { getKlaytnRoyaltyClaimTransactionHistory } from 'app/modules/accounts/components/settings/_redux/accountCrud'
import { getETHRoyaltyClaimTransactionHistoryData } from 'app/modules/dropDetail/dropDetailEthereum/queries/Queries'
import { UserRoyaltyClaimHistory } from './components/UserRoyaltyClaim'
import { UserCardHistory } from './components/UserOrderDetail'
import { UserTransferHistory } from './components/UserTransferDetail'
import { CircularProgress } from '@mui/material'


const ViewUserDetail = (props: any) => {

    const {state} = useLocation();
    const userID = state;
    const [checked, setChecked] = useState(true);
    const [loading, setLoading] = useState(true);

    const [userDetail, setUserDetail] = useState<any>(null)
    const [orderHistory, setOrderHistory] = useState<Array<any>>([])
    const [transferHistory, setTransferHistory] = useState<Array<any>>([])
    const [ethRoyaltyClaimData, setEthRoyaltyClaimData] = useState<any>([])
    const [klayRoyaltyClaimData, setKlayRoyaltyClaimData] = useState<any>([])

    const [showUserUpdateDialog, setShowUserUpdateDialog] = useState<boolean>(false)
    const [showPasswordUpdateDialog, setShowPasswordUpdateDialog] = useState<boolean>(false)

    const successMsg = localStorage.getItem('resSuccess')
    const [resSuccessMessage, setResSuccessMessage] = useState<string | null>(successMsg)

    const failureMsg = localStorage.getItem('resFailure')
    const [resFailureMessage, setResFailureMessage] = useState<string | null>(failureMsg)

    if(resSuccessMessage !== null && resSuccessMessage !== undefined) {
        setTimeout(() => {
          setChecked(false);
          localStorage.removeItem('resSuccess')
          setTimeout(() => {
            setResSuccessMessage(null);
          }, 300);
        }, 5000);
    }
    
    if(resFailureMessage !== null && resFailureMessage !== undefined) {
        setTimeout(() => {
            setChecked(false);
            localStorage.removeItem('resFailure')
            setTimeout(() => {
                setResFailureMessage(null);
            }, 300);
        }, 5000);
    }

    useEffect(() => {
        getUserDetail(userID)
        .then((response) => {
            setUserDetail(response.data.user_detail)
            setOrderHistory(response.data.order_history)
            setTransferHistory(response.data.transfer_history)
        })
    }, [])

    useEffect(() => {
        if(userDetail) {
            getKlaytnRoyaltyClaimTransactionHistory(userDetail.kaikas_wallet)
            .then((response) => {
                if(response.data.success) {
                    setKlayRoyaltyClaimData(response.data.claimData)
                    // setLoading(false)
                    getETHRoyaltyClaimTransactionHistoryData(userDetail.metamask_wallet)
                    .then((data: any) => {
                        setEthRoyaltyClaimData(data)
                        setLoading(false)
                    })
                }
            })
        }

    }, [userDetail])

    return (
        <>  
           {!loading && 
                <div className="post d-flex flex-column-fluid" id="kt_post">
                    <div id="kt_content_container" className="container">
                        <div className="d-flex flex-column flex-xl-row">
                            <div className="flex-column flex-lg-row-auto w-100 w-xl-400px mb-10">
                                <UserSummary 
                                    userID={userID} 
                                    showUserUpdateDialog={showUserUpdateDialog}
                                    setShowUserUpdateDialog={setShowUserUpdateDialog}
                                    userDetail={userDetail}
                                />
                                <ConnectedAccounts 
                                    userID={userID} 
                                    userDetail={userDetail}
                                />
                            </div>
                            <div className="flex-lg-row-fluid ms-lg-15">
                                {resSuccessMessage !== null && resSuccessMessage !== undefined && resSuccessMessage !== ""  ? (
                                    <AlertSuccess message={resSuccessMessage} checked={checked} />
                                ) : null}

                                {resFailureMessage !== null && resFailureMessage !== undefined && resFailureMessage !== ""  ? (
                                    <AlertDanger message={resFailureMessage} checked={checked} />
                                ) : null}
                                {/* <ViewUserHeader /> */}
                                <div className="tab-content" id="myTabContent">
                                    <div className="tab-pane show active" id="kt_user_view_overview_security" role="tabpanel">
                                        <UserUpdateCard 
                                            userID = {userID} 
                                            showPasswordUpdateDialog={showPasswordUpdateDialog}
                                            setShowPasswordUpdateDialog={setShowPasswordUpdateDialog}
                                        />

                                        <UserCardHistory 
                                            orderHistory={orderHistory}
                                        />

                                        <UserTransferHistory 
                                            transferHistory={transferHistory}
                                        />

                                        <UserRoyaltyClaimHistory 
                                            ethClaimData={ethRoyaltyClaimData}
                                            klayClaimData={klayRoyaltyClaimData}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <UserUpdateWithAddress 
                            userID={userID} 
                            showUserUpdateDialog={showUserUpdateDialog}
                            setShowUserUpdateDialog={setShowUserUpdateDialog}
                            userDetail={userDetail}
                        />
                        <ShowWalletAddress userID={userID} />
                        <UpdatePassword 
                            userID={userID} 
                            showPasswordUpdateDialog={showPasswordUpdateDialog}
                            setShowPasswordUpdateDialog={setShowPasswordUpdateDialog}
                        />
                    </div>
                </div>
           }

           {loading && 
                <div className='d-flex w-100 h-100'>
                    <div className='my-auto mx-auto'>
                        <div className='d-flex flex-column justify-content-center'>
                            <span className='text-primary fs-3'>Loading</span> <br />
                            <span className='ms-4'>
                                <CircularProgress />
                            </span>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

export default ViewUserDetail