import { useState } from 'react'
import { useSelector, shallowEqual, useDispatch } from 'react-redux'
import { useLocation, useNavigate } from 'react-router'
import { Button } from '@mui/material';
import { Dialog } from '@mui/material';
import { DialogActions } from '@mui/material';
import { DialogContent } from '@mui/material';
import { DialogTitle } from '@mui/material';
import {toAbsoluteUrl} from '../../../../../_metronic/helpers'
import {languages, timeZones} from '../../../accounts/components/settings/LangAndTimeZone'
import * as Yup from 'yup'
import {useFormik} from 'formik'
import { RootState } from '../../../../../setup'
import * as admin from '../../../auth/redux/AuthRedux'
import { updateUserForAdmin } from '../../api';
import { FormattedMessage } from 'react-intl';

const adminUserDetailsSchema = Yup.object().shape({
    fullname: Yup.string().required('Full Name is required'),
    userName: Yup.string().required('Username is required'),
    email: Yup.string()
      .email('Wrong email format')
      .min(3, 'Minimum 3 symbols')
      .max(50, 'Maximum 50 symbols')
      .required('Email is required'),
    // isAdminUser: Yup.boolean(),
    description: Yup.string().notRequired(),
    adminPassword: Yup.string().min(6, 'Require mininmum 6 characters').required('Admin Password is required')
})

interface AdminUserDetailModel {
    fullName: string
    userName: string
    email: string
    // isAdminUser: boolean
    description: string
    adminPassword: string
}

export const UserUpdateWithAddress = (props: any) => {

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const [loading, setLoading] = useState<boolean>(false)
    const [image, setImage] = useState<any>("")
    const [coverImage, setCoverImage] = useState<any>("")
    const [password, setPassword] = useState<string>('')
    

    const { adminID, authToken, isAdmin }: any = useSelector<RootState>(
        ({auth}) => ({
            adminID: auth.user?.userID,
            authToken: auth.accessToken,
            isAdmin: auth.user?.isAdmin
        }), shallowEqual
    )

    const [_isAdmin, setIsAdmin] = useState<boolean>(props.userDetail.is_admin == '1' ? true : false)
    

    const initialValues: AdminUserDetailModel = {
        fullName: props.userDetail.full_name,
        userName: props.userDetail.username,
        email: props.userDetail.email,
        description: props.userDetail.description,
        adminPassword: ''
    }

    const handleImageChange = (e: any) => {
        setImage(e.target.files[0])
    };

    const handleCoverImageChange = (e: any) => {
        setCoverImage(e.target.files[0])
    }

    const formik = useFormik({
        initialValues,
        // validationSchema: adminUserDetailsSchema,
        onSubmit: (values) => {
            setLoading(true)
            setTimeout(() => {
                const formData = new FormData()
                formData.append('userID', props.userID)
                formData.append('authToken', authToken)
                formData.append('handle', values.userName)
                if(image !== "") {
                    formData.append('userImage', image)
                }
                if(coverImage !== "") {
                    formData.append('coverImage', coverImage)
                }
                formData.append('name', values.fullName)
                formData.append('email', values.email)
                formData.append('isAdmin', _isAdmin ? '1' : '0')
                formData.append('description', values.description)
                formData.append('adminID', adminID)
                formData.append('adminPassword', values.adminPassword)

                updateUserForAdmin(formData)
                .then((response) => {
                    if(response.data.success) {
                        localStorage.setItem('resSuccess', response.data.message)
                        // window.location.reload()
                        setLoading(false)
                    } else {
                        if(response.data.message) {
                            localStorage.setItem('resFailure', response.data.message)
                            // window.location.reload()
                            setLoading(false)
                        } else {
                            localStorage.setItem('resFailure', 'Failed to update the profile')
                            // window.location.reload()
                            setLoading(false)
                        }
                    }
                })
                // .catch((error) => {
                //     // dispatch(admin.actions.signOut())
                //     localStorage.setItem('sessionTimeOut', 'Access denied. Session timed out.')
                //     navigate('/admin')
                // })

            }, 1000)
        }
    })

    const handleIsAdminChange = () => {
        setIsAdmin(!_isAdmin)
    }

    return (
        <>
            <Dialog 
                open={props.showUserUpdateDialog} 
                onClose={() => props.setShowUserUpdateDialog(false)} 
                aria-labelledby="form-dialog-title"
            >
                <DialogTitle className='px-5 text-center fw-bolder text-muted' id="form-dialog-title">
                    <div className="modal-header" id="kt_modal_update_user_header">
                        <h2 className="fw-bolder">
                            <FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.UPDATE_USER_DETAILS" />
                        </h2>
                        <div className="btn btn-icon btn-sm btn-active-icon-primary" onClick={() => props.setShowUserUpdateDialog(false)}>
                            <span className="svg-icon svg-icon-1">
                                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                    <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#000000">
                                        <rect fill="#000000" x="0" y="7" width="16" height="2" rx="1" />
                                        <rect fill="#000000" opacity="0.5" transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000)" x="0" y="7" width="16" height="2" rx="1" />
                                    </g>
                                </svg>
                            </span>
                        </div>
                    </div>
                </DialogTitle>
                <div className='separator'></div>
                <DialogContent>
                    <form style={{ padding: '10px 50px' }} className="form" onSubmit={formik.handleSubmit} id="kt_modal_update_user_form">
                        <div 
                            className="modal-body py-10 px-lg-17"
                            style={{ overflow: 'auto' }}
                        >
                            <div className="d-flex flex-column scroll-y me-n7 pe-7" id="kt_modal_update_user_scroll" data-kt-scroll="true" data-kt-scroll-activate="{default: false, lg: true}" data-kt-scroll-max-height="auto" data-kt-scroll-dependencies="#kt_modal_update_user_header" data-kt-scroll-wrappers="#kt_modal_update_user_scroll" data-kt-scroll-offset="300px">
                                <div 
                                    className="fw-boldest fs-3 rotate collapsible mb-7" 
                                    role='button'
                                    data-bs-toggle='collapse'
                                    data-bs-target='#kt_modal_update_user_user_info'
                                    aria-expanded='true'
                                    aria-controls='kt_modal_update_user_user_info'
                                >
                                    <FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.USER_INFO" />
                                <span className="ms-2 rotate-180">
                                    <span className="svg-icon svg-icon-3">
                                        <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                <polygon points="0 0 24 0 24 24 0 24" />
                                                <path d="M6.70710678,15.7071068 C6.31658249,16.0976311 5.68341751,16.0976311 5.29289322,15.7071068 C4.90236893,15.3165825 4.90236893,14.6834175 5.29289322,14.2928932 L11.2928932,8.29289322 C11.6714722,7.91431428 12.2810586,7.90106866 12.6757246,8.26284586 L18.6757246,13.7628459 C19.0828436,14.1360383 19.1103465,14.7686056 18.7371541,15.1757246 C18.3639617,15.5828436 17.7313944,15.6103465 17.3242754,15.2371541 L12.0300757,10.3841378 L6.70710678,15.7071068 Z" fill="#000000" fillRule="nonzero" transform="translate(12.000003, 11.999999) rotate(-180.000000) translate(-12.000003, -11.999999)" />
                                            </g>
                                        </svg>
                                    </span>
                                </span></div>
                                <div id="kt_modal_update_user_user_info" className="show">
                                    <div className="mb-7">
                                        <label className="fs-6 fw-bold mb-2">
                                            <span>
                                                <FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.UPADTE_AVATAR" />
                                            </span>
                                            <i className="fas fa-exclamation-circle ms-1 fs-7" data-bs-toggle="tooltip" title="Allowed file types: png, jpg, jpeg."></i>
                                        </label>
                                        <div className="mt-1">
                                            <div className="image-input image-input-outline" data-kt-image-input="true" style={{backgroundImage: `url(${toAbsoluteUrl('/media/avatars/blank.png')})`}}>
                                                <div 
                                                    className="image-input-wrapper w-125px h-125px" 
                                                    style={{backgroundImage: `url(${image === "" ? `${process.env.REACT_APP_API_USER_IMAGE_URL}/${props.userDetail.user_image}` : URL.createObjectURL(image)})`}}
                                                ></div>
                                                <label className="btn btn-icon btn-circle btn-active-color-primary w-25px h-25px bg-body shadow" data-kt-image-input-action="change" data-bs-toggle="tooltip" title="Change avatar">
                                                    <i className="bi bi-pencil-fill fs-7"></i>
                                                    <input onChange={(e) => handleImageChange(e)} type="file" name="avatar" accept=".png, .jpg, .jpeg" />
                                                    <input type="hidden" name="avatar_remove" />
                                                </label>
                                                <span className="btn btn-icon btn-circle btn-active-color-primary w-25px h-25px bg-body shadow" data-kt-image-input-action="cancel" data-bs-toggle="tooltip" title="Cancel avatar">
                                                    <i className="bi bi-x fs-2"></i>
                                                </span>
                                                <span className="btn btn-icon btn-circle btn-active-color-primary w-25px h-25px bg-body shadow" data-kt-image-input-action="remove" data-bs-toggle="tooltip" title="Remove avatar">
                                                    <i className="bi bi-x fs-2"></i>
                                                </span>
                                            </div>
                                        </div>
                                    </div>

                                    <div className='mb-10 fv-row'>
                                        <label className='form-label mb-3'>
                                            <FormattedMessage id='ADMIN.VIEW_USER.UPDATE_USER.UPADTE_USER_COVER' />
                                        </label>

                                            {coverImage == "" ? (
                                                <div>
                                                    {props.userDetail.cover_image == 'default' ? (
                                                        <>
                                                            <img 
                                                                src={`${process.env.REACT_APP_API_USER_COVER_IMAGE_URL}/default.jpeg`} 
                                                                className='w-400px mb-3' 
                                                            />
                                                        </>
                                                    ) : (
                                                        <>
                                                            <img 
                                                                src={`${process.env.REACT_APP_API_USER_COVER_IMAGE_URL}/${props.userDetail.cover_image}`} 
                                                                className='w-400px mb-3' 
                                                            />
                                                        </>
                                                    )}
                                                </div>
                                            ): (
                                                <div>
                                                    <img src={URL.createObjectURL(coverImage)} className='w-400px mb-3' />
                                                </div>
                                            )}

                                        <div>
                                            <input type="file" id="image" accept="image/png, image/jpeg" onChange={(e) => handleCoverImageChange(e)} />
                                        </div>
                                    </div>
                                    
                                    <div className="fv-row mb-7">
                                        <label className="required fs-6 fw-bold mb-2"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.FULLNAME" /></label>
                                        <input 
                                            type="text" 
                                            className="form-control form-control-solid" 
                                            placeholder="Full Name" 
                                            {...formik.getFieldProps('fullName')}
                                        />
                                        {formik.touched.fullName && formik.errors.fullName && (
                                            <div className='fv-plugins-message-container'>
                                                <div className='fv-help-block'>{formik.errors.fullName}</div>
                                            </div>
                                        )}
                                    </div>
                                    <div className="fv-row mb-7">
                                        <label className="required fs-6 fw-bold mb-2"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.USERNAME" /></label>
                                        <input 
                                            type="text" 
                                            className="form-control form-control-solid" 
                                            placeholder="User Name" 
                                            {...formik.getFieldProps('userName')}
                                        />
                                        {formik.touched.userName && formik.errors.userName && (
                                            <div className='fv-plugins-message-container'>
                                                <div className='fv-help-block'>{formik.errors.userName}</div>
                                            </div>
                                        )}
                                    </div>
                                    <div className="fv-row mb-7">
                                        <label className="required fs-6 fw-bold mb-2">
                                            <span><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.EMAIL" /></span>
                                            <i className="fas fa-exclamation-circle ms-1 fs-7" data-bs-toggle="tooltip" title="Email address must be active"></i>
                                        </label>
                                        <input 
                                            type="email" 
                                            className="form-control form-control-solid" 
                                            placeholder="Email" 
                                            {...formik.getFieldProps('email')}
                                        />
                                        {formik.touched.email && formik.errors.email && (
                                            <div className='fv-plugins-message-container'>
                                                <div className='fv-help-block'>{formik.errors.email}</div>
                                            </div>
                                        )}
                                    </div>

                                    <div className="fv-row mb-7">
                                        {/* <label className='form-check form-check-custom form-check-solid align-items-start'>
                                            <span className='form-check-label d-flex flex-column align-items-start'>
                                                <span className='fw-bold fs-6 mb-0'><FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.IS_ADMIN" /></span>
                                            </span>
                                            <input
                                                className='form-check-input ms-3'
                                                type='checkbox'
                                                // defaultChecked={_isAdmin}
                                                // onChange={handleIsAdminChange}
                                                {...formik.getFieldProps('isAdminUser')}
                                            />
                                        </label> */}
                                        <label className="required fs-6 fw-bold mb-2">
                                            <span><FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.IS_ADMIN" /></span>
                                            <i className="fas fa-exclamation-circle ms-1 fs-7"></i>
                                        </label>
                                        <input 
                                            type="checkbox" 
                                            className="form-check-input ms-3" 
                                            defaultChecked={_isAdmin}
                                            onChange={handleIsAdminChange}
                                        />
                                    </div>

                                    <div className="fv-row mb-7">
                                        <label className="required fs-6 fw-bold mb-2"><FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.DESCRIPTION" /></label>
                                        <textarea  
                                            className="form-control form-control-solid" 
                                            rows={6}
                                            {...formik.getFieldProps('description')}
                                        />

                                        {formik.touched.description && formik.errors.description && (
                                            <div className='fv-plugins-message-container'>
                                                <div className='fv-help-block text-danger'>{formik.errors.description}</div>
                                            </div>
                                        )}
                                    </div>

                                    <div className="fv-row mb-7">
                                        <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.USER.ADD_USER.ADMIN_PASSWORD" /></label>
                                        <input 
                                            type="password" 
                                            className="form-control form-control-solid mb-3 mb-lg-0" 
                                            placeholder="Admin Password" 
                                            {...formik.getFieldProps('adminPassword')}
                                        />
                                        {formik.touched.adminPassword && formik.errors.adminPassword && (
                                            <div className='fv-plugins-message-container'>
                                                <div className='fv-help-block text-danger'>{formik.errors.adminPassword}</div>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="modal-footer flex-center">
                            <button 
                                type="reset" 
                                className="btn btn-light me-3" 
                                onClick={() => props.setShowUserUpdateDialog(false)}
                            >
                                <FormattedMessage id="ADMIN.VIEW_USER.UPDATE_USER.DISCARD" />
                            </button>
                            <button type="submit" className="btn btn-primary">
                                <span className="indicator-label"><FormattedMessage id="HOME_PAGE.SUBMIT" /></span>
                                {loading &&
                                    <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                                }
                            </button>
                        </div>
                    </form>
                </DialogContent>
            </Dialog>
        </>
    )
}